import { Route, Redirect } from "react-router-dom";

const RouteComponent = ({
  isAuth,
  path,
  pathnameRedirect,
  component: Component,

  ...rest
}) => (
  <Route
    {...rest}
    exact
    path={path}
    render={() => (isAuth ? <Component /> : <Redirect to={pathnameRedirect} />)}
  />
);

export default RouteComponent;
