import { Fragment, useEffect, useContext } from "react";
import { privateInfo, logout } from "../API";

import image from "../assets/private.gif";
import UserContext from "../components/UserContext";

const Home = () => {
  const { logoutDispatch } = useContext(UserContext);

  useEffect(() => {
    (async () => {
      const response = await privateInfo();
      console.log(response);
    })();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();

    await logout();
    logoutDispatch();
  };

  return (
    <Fragment>
      <div className="container">
        <h1 className="title">Ruta privada</h1>

        <img src={image} alt="private-gif" />
        <p>Welcome to private route</p>

        <div className="btnContainear">
          <button className="btn" onClick={handleSubmit}>
            logout
          </button>
        </div>
      </div>
    </Fragment>
  );
};

export default Home;
