export const INITIAL_STATE = {
  user: null,
};

export const reducer = (state, action) => {
  switch (action.type) {
    case "login": {
      const { email } = action.payload;
      return {
        ...state,
        user: { email }, // assign user here
      };
    }

    case "logout":
      return {
        ...state,
        user: null,
      };

    default:
      throw new Error(`Invalid action type: ${action.type}`);
  }
};
