import { Fragment, useState, useContext } from "react";
import { useHistory } from "react-router";

import { login } from "../API";
import useInput from "../hooks/useInput";
import UserContext from "../components/UserContext";

const Login = () => {
  const history = useHistory();
  const { loginDispatch } = useContext(UserContext);

  const { handleChange, setErr, inputs, err } = useInput();
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();

    setLoading(true);

    if (!inputs.email || !inputs.password)
      setErr("Ingrese un email y una contraseña");

    const response = await login(inputs);

    setLoading(false);

    if (response.status === 500) return setErr(response.data);
    loginDispatch(response.email);
  };

  const goRegister = (e) => {
    e.preventDefault();
    history.push("/auth/register");
  };

  return (
    <Fragment>
      <form onSubmit={handleSubmit} className="form">
        <h1 className="title">Iniciar Sesion</h1>

        <div className="inputContainer">
          <input
            type="text"
            name="email"
            className="input"
            placeholder="Correo Electrónico"
            value={inputs.email}
            onChange={handleChange}
          />
        </div>

        <div className="inputContainer">
          <input
            name="password"
            type="password"
            className="input"
            placeholder="Contraseña"
            value={inputs.password}
            onChange={handleChange}
          />
        </div>

        {err && <small className="error">{err}</small>}

        <div className="btnContainear">
          <button className="btn" disabled={loading} type="submit">
            ingresar
          </button>
        </div>

        <small className="link" onClick={goRegister}>
          Registrarse
        </small>
      </form>
    </Fragment>
  );
};

export default Login;
