import { useState } from "react";

const useInput = () => {
  const [err, setErr] = useState("");
  const [inputs, setInputs] = useState({ email: "", password: "" });

  const handleChange = (e) => {
    if (err) setErr("");
    setInputs({ ...inputs, [e.target.name]: e.target.value });
  };

  return {
    err,
    inputs,
    setErr,
    handleChange,
  };
};

export default useInput;
